import React, { useEffect, useMemo, useRef, useState } from "react";
import "./whyChooseUs.css";
import {
  FaUserCheck,
  FaTools,
  FaChartLine,
  FaHandshake,
  FaArrowRight,
} from "react-icons/fa";

type StatItem = {
  label: string;
  value: number;
  suffix?: string;
};

type BenefitItem = {
  title: string;
  desc: string;
  icon: React.ReactNode;
};

function useCountUpOnView(target: number, durationMs = 1200) {
  const ref = useRef<HTMLDivElement | null>(null);
  const [value, setValue] = useState(0);
  const [hasRun, setHasRun] = useState(false);

  useEffect(() => {
    if (!ref.current) return;

    const el = ref.current;

    const observer = new IntersectionObserver(
      (entries) => {
        const entry = entries[0];
        if (!entry.isIntersecting) return;
        if (hasRun) return;

        setHasRun(true);

        const start = performance.now();
        const from = 0;

        const tick = (now: number) => {
          const progress = Math.min((now - start) / durationMs, 1);
          const eased = 1 - Math.pow(1 - progress, 3); // easeOutCubic
          const next = Math.round(from + (target - from) * eased);

          setValue(next);

          if (progress < 1) requestAnimationFrame(tick);
        };

        requestAnimationFrame(tick);
      },
      { threshold: 0.35 }
    );

    observer.observe(el);

    return () => observer.disconnect();
  }, [target, durationMs, hasRun]);

  return { ref, value };
}

const StatCard: React.FC<StatItem> = ({ label, value, suffix }) => {
  const { ref, value: animated } = useCountUpOnView(value, 1300);

  return (
    <div className="wcuStatCard" ref={ref}>
      <div className="wcuStatValue">
        {animated}
        {suffix || ""}
      </div>
      <div className="wcuStatLabel">{label}</div>
    </div>
  );
};

