import React, { useEffect, useState } from "react";
import { FaCheck, FaPhone, FaShieldAlt, FaTools, FaChartLine } from "react-icons/fa";
import "./hero.css";

interface FormData {
  fullName: string;
  phone: string;
  email: string;
  propertyAddress: string;
  propertyType: string;
}

/* ===== Testimonials Data ===== */
const testimonials = [
  {
    id: 1,
    text: "Professional team, great communication, and quick response to maintenance requests.",
    author: "Sarah M., Property Owner",
    stars: 5,
  },
  {
    id: 2,
    text: "They placed a great tenant fast and handled everything smoothly. Highly recommend.",
    author: "James R., Investor",
    stars: 5,
  },
  {
    id: 3,
    text: "Monthly reporting is clear and on time. Finally, stress-free property management service.",
    author: "Emily T., Landlord",
    stars: 5,
  },
];

const TestimonialCarousel = ({ isPaused }: { isPaused: boolean }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    if (isPaused) return;

    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) =>
        prevIndex === testimonials.length - 1 ? 0 : prevIndex + 1
      );
    }, 4000);

    return () => clearInterval(interval);
  }, [isPaused]);

  const renderStars = (count: number) => {
    return Array.from({ length: count }, (_, i) => (
      <span key={i} className="reviewStar" aria-hidden="true">
        ★
      </span>
    ));
  };

  return (
    <div className="reviewSlider" aria-label="Customer Reviews">
      <div className="reviewSlider__inner">
        <div className="reviewStars">{renderStars(testimonials[currentIndex].stars)}</div>

        <p className="reviewText">
          “{testimonials[currentIndex].text}”
        </p>

        <p className="reviewAuthor">
          — {testimonials[currentIndex].author}
        </p>
      </div>

      <div className="reviewDots" aria-label="Review Navigation">
        {testimonials.map((_, index) => (
          <button
            key={index}
            className={`reviewDot ${index === currentIndex ? "active" : ""}`}
            onClick={() => setCurrentIndex(index)}
            aria-label={`Go to review ${index + 1}`}
            type="button"
          />
        ))}
      </div>
    </div>
  );
};

const Hero: React.FC = () => {
  const [formData, setFormData] = useState<FormData>({
    fullName: "",
    phone: "",
    email: "",
    propertyAddress: "",
    propertyType: "Single Family",
  });

  const [isSubmitted, setIsSubmitted] = useState(false);
  const [errors, setErrors] = useState<Partial<FormData>>({});

  /* Pause slider logic */
  const [isFormFocused, setIsFormFocused] = useState(false);
  const [isHovered, setIsHovered] = useState(false);
  const isPaused = isFormFocused || isHovered;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));

    if (errors[name as keyof FormData]) {
      setErrors((prev) => ({
        ...prev,
        [name]: undefined,
      }));
    }
  };

  const validateForm = () => {
    const newErrors: Partial<FormData> = {};

    if (!formData.fullName.trim()) {
      newErrors.fullName = "Name is required";
    }

    if (!formData.phone.trim()) {
      newErrors.phone = "Phone is required";
    } else if (!/^\d{10,}$/.test(formData.phone.replace(/\D/g, ""))) {
      newErrors.phone = "Valid phone number required";
    }

    if (!formData.email.trim()) {
      newErrors.email = "Email is required";
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = "Valid email required";
    }

    return newErrors;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const validationErrors = validateForm();

    if (Object.keys(validationErrors).length === 0) {
      console.log("Form submitted:", formData);
      setIsSubmitted(true);

      setTimeout(() => {
        setIsSubmitted(false);
        setFormData({
          fullName: "",
          phone: "",
          email: "",
          propertyAddress: "",
          propertyType: "Single Family",
        });
      }, 3000);
    } else {
      setErrors(validationErrors);
    }
  };

  const trustPoints = [
    {
      icon: <FaShieldAlt />,
      title: "Tenant Screening",
      description: "Thorough background checks",
    },
    {
      icon: <FaTools />,
      title: "Maintenance Handling",
      description: "24/7 emergency response",
    },
    {
      icon: <FaChartLine />,
      title: "Monthly Reporting",
      description: "Detailed financial reports",
    },
  ];

  return (
    <section className="hero-section" aria-label="Property Management Services">
      {/* Background Accent Shapes */}
      <div className="hero-background-accent" aria-hidden="true"></div>
      <div className="hero-background-accent-left" aria-hidden="true"></div>

      <div className="hero-container">
        <div className="hero-content">
          {/* Badge */}
          <div className="hero-badge" role="status">
            <FaCheck className="badge-icon12" aria-hidden="true" />
            <span>Trusted Property Management</span>
          </div>

          {/* Headline */}
          <h1 className="hero-headline">
            <span className="headline-line">Better Tenants. Better Returns.</span>
            <span className="headline-line accent">Less Hassle.</span>
          </h1>

          {/* Description */}
          <p className="hero-description">
            Professional property management that maximizes your rental income
            while minimizing your stress. Get the peace of mind you deserve.
          </p>

          {/* CTA Buttons */}
          <div className="hero-cta-buttons">
            <button
              className="cta-button primary"
              onClick={() =>
                document.getElementById("quote-form")?.scrollIntoView({ behavior: "smooth" })
              }
              aria-label="Get a free rental analysis"
            >
              Get Free Rental Analysis
            </button>

            <a href="tel:+16035083888" className="cta-button secondary" aria-label="Call us now">
              <FaPhone className="button-icon" aria-hidden="true" />
              Call Now
            </a>
          </div>

          {/* Trust Points */}
          <div className="trust-points">
            {trustPoints.map((point, index) => (
              <div key={index} className="trust-point">
                <div className="trust-icon" aria-hidden="true">
                  {point.icon}
                </div>
                <div className="trust-content">
                  <h3 className="trust-title">{point.title}</h3>
                  <p className="trust-description">{point.description}</p>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Quote Form Card */}
        <div
          className="quote-form-card"
          id="quote-form"
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}
        >
          {/* ✅ Review Slider Added (Top of Form) */}
          <TestimonialCarousel isPaused={isPaused} />

          <div className="form-header">
            <h2 className="form-title">Request a Free Quote</h2>
            <p className="form-subtitle">Start maximizing your rental income today</p>
          </div>

          {isSubmitted ? (
            <div className="success-message" role="alert">
              <div className="success-icon" aria-hidden="true">
                ✓
              </div>
              <h3>Thank You!</h3>
              <p>We'll contact you within 24 hours with your free analysis.</p>
            </div>
          ) : (
            <form onSubmit={handleSubmit} className="quote-form" noValidate>
              <div className="form-group">
                <label htmlFor="fullName" className="form-label">
                  Full Name *
                </label>
                <input
                  type="text"
                  id="fullName"
                  name="fullName"
                  value={formData.fullName}
                  onChange={handleChange}
                  className={`form-input ${errors.fullName ? "error" : ""}`}
                  placeholder="John Smith"
                  required
                  aria-required="true"
                  aria-invalid={!!errors.fullName}
                  aria-describedby={errors.fullName ? "fullName-error" : undefined}
                  onFocus={() => setIsFormFocused(true)}
                  onBlur={() => setIsFormFocused(false)}
                />
                {errors.fullName && (
                  <span id="fullName-error" className="error-message" role="alert">
                    {errors.fullName}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="phone" className="form-label">
                  Phone Number *
                </label>
                <input
                  type="tel"
                  id="phone"
                  name="phone"
                  value={formData.phone}
                  onChange={handleChange}
                  className={`form-input ${errors.phone ? "error" : ""}`}
                  placeholder="(555) 123-4567"
                  required
                  aria-required="true"
                  aria-invalid={!!errors.phone}
                  aria-describedby={errors.phone ? "phone-error" : undefined}
                  onFocus={() => setIsFormFocused(true)}
                  onBlur={() => setIsFormFocused(false)}
                />
                {errors.phone && (
                  <span id="phone-error" className="error-message" role="alert">
                    {errors.phone}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="email" className="form-label">
                  Email Address *
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  className={`form-input ${errors.email ? "error" : ""}`}
                  placeholder="john@example.com"
                  required
                  aria-required="true"
                  aria-invalid={!!errors.email}
                  aria-describedby={errors.email ? "email-error" : undefined}
                  onFocus={() => setIsFormFocused(true)}
                  onBlur={() => setIsFormFocused(false)}
                />
                {errors.email && (
                  <span id="email-error" className="error-message" role="alert">
                    {errors.email}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="propertyAddress" className="form-label">
                  Property Address / City
                </label>
                <input
                  type="text"
                  id="propertyAddress"
                  name="propertyAddress"
                  value={formData.propertyAddress}
                  onChange={handleChange}
                  className="form-input"
                  placeholder="Enter property location"
                  onFocus={() => setIsFormFocused(true)}
                  onBlur={() => setIsFormFocused(false)}
                />
              </div>

          
              <button type="submit" className="submit-button">
                Get My Free Quote
              </button>

              <p className="form-disclaimer">No obligation. We respond within 24 hours.</p>
            </form>
          )}
        </div>
      </div>
    </section>
  );
};

export default Hero;
