import React from "react";
import "./services.css";
import { FaBuilding, FaHome, FaChartLine, FaKey, FaArrowRight } from "react-icons/fa";
import { HiOutlineArrowNarrowRight } from "react-icons/hi";

type CardItem = {
  title: string;
  desc: string;
  icon: React.ReactNode;
  imageUrl: string;
  href: string;
};

export default function CategoryCardsSection() {
  const handleRentalsClick = (e: React.MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
    document.getElementById('rentals')?.scrollIntoView({ behavior: 'smooth' });
  };

  const cards: CardItem[] = [
    {
      title: "Residential Management",
      desc: "Single-family & multifamily management with tenant screening and full support.",
      icon: <FaHome />,
      imageUrl:
        "https://images.unsplash.com/photo-1621872550846-af5b7091a155?q=80&w=1267&auto=format&fit=crop&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
      href: "/residential-management",
    },
    {
      title: "Commercial Management",
      desc: "Professional commercial property oversight to protect your investment.",
      icon: <FaBuilding />,
      imageUrl:
        "https://images.unsplash.com/photo-1486406146926-c627a92ad1ab?auto=format&fit=crop&w=1400&q=80",
      href: "/commercial-property-management",
    },
    {
      title: "Investor Corner",
      desc: "Reports, ROI planning, and long-term strategies for rental growth.",
      icon: <FaChartLine />,
      imageUrl:
        "https://images.unsplash.com/photo-1554224155-6726b3ff858f?auto=format&fit=crop&w=1400&q=80",
      href: "/investors-corner",
    },
    {
      title: "Find a Rental Home",
      desc: "Browse available rentals and get help finding the right place fast.",
      icon: <FaKey />,
      imageUrl:
        "https://images.unsplash.com/photo-1560518883-ce09059eeffa?auto=format&fit=crop&w=1400&q=80",
      href: "#rentals",
    },
  ];

  return (
    <section className="catSection" id="services">
      <div className="catContainer">
        <div className="catHeadingWrap">
          <h2 className="catTitle">What Are You Looking For?</h2>
          <div className="catUnderline" />
          <p className="catSubtitle">
            Choose a category to explore our services and get started quickly.
          </p>
        </div>

        <div className="catGrid">
          {cards.map((card, idx) => (
            <a 
              className="catCard" 
              href={card.href} 
              key={idx}
              onClick={card.href === '#rentals' ? handleRentalsClick : undefined}
            >
              <div 
                className="catCardBg"
                style={{ backgroundImage: `url(${card.imageUrl})` }}
              />
              <div className="catOverlay" />
              
              <div className="catCardHeader">
                <div className="catIcon">{card.icon}</div>
                <h3 className="catCardTitle">{card.title}</h3>
              </div>
              
              <div className="catCardBody">
                <p className="catDesc">{card.desc}</p>
              </div>
              
              <div className="catCardFooter">
                <div className="catExplore">
                  <span className="catExploreText">Explore Service</span>
                  <HiOutlineArrowNarrowRight className="catArrow" />
                </div>
              </div>
            </a>
          ))}
        </div>
      </div>
    </section>
  );
}
